* {
	box-sizing: border-box;
  overflow: hidden;
}

body {
	width: 100%;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #000000;
}

.btn {
	background-color: white;
	color: #e5dc15;
	font-family: 'Montserrat', sans-serif;
	text-transform: uppercase;
	margin: 15px;
	padding: 5px;
	border: 2px solid #e5dc15;
	border-radius: 5px;
	box-shadow: 0 12px #c2bb11, 0 18px rgba(0, 0, 0, 0.4);
  font-size: 60px;
  width: 90px;
  height: 80px;
  cursor: pointer;
  position:relative;
  top:0;
}

.btn:hover{
  background-color: #e5dc15;
  color:#000000;
  top:4px;
  box-shadow: 0 8px #c2bb11, 0 14px rgba(0, 0, 0, 0.4);
  transition: 0.2s;
}

.btn:active{
  top:8px;
  box-shadow: 0 4px #c2bb11, 0 10px rgba(0, 0, 0, 0.4);
  transition: 0.1s;
}

.game{
  display: flex;
  flex-wrap: wrap;
}

.game-info{
  margin:15px;
  color:white;
}

.board > div {
  display: flex;
}